/*
==========================================================================
   #Links
========================================================================== */

a {
    text-decoration: underline;
    color: inherit;
    transition: color $transition-time;

    &:focus,
    &:hover {
        color: $color-link-hover;
        text-decoration: underline;
    }

    &:focus {
        outline: $focus-border-width solid $color-focus;
    }
}
