/*
==========================================================================
   #Mega menu
========================================================================== */
$mega-menu-tb-width: 320px;
$menu-transition-time: .5s;

.page-head-bottom-menus {
    position: absolute;
    padding-top: device-map($head-height, tablet);
    right: 0;
    top: 0;
    height: 100vh;
    transform: translateX(100%);
    transition: all $menu-transition-time;
    z-index: 30;
    background-color: $color-mega-menu-bg;

    &.active {
        visibility: visible;
        transition: all $menu-transition-time;
        transform: translateX(0);
    }

    @include breakpoint(tablet) {
        width: $mega-menu-tb-width;
        box-sizing: content-box;
    }

    @include breakpoint(mobile) {
        width: 100%;
    }
}

.page-head-bottom-menus {

    &.active {
        left: calc(100% - $mega-menu-tb-width);

        .mega-menu {
            overflow-y: scroll;
            height: 100%;
        }
    }
}

/*
 * Mefa menu style
 */

.mega-menu {
    display: none;

    @include breakpoint(not-desktop) {
        display: block;
    }

    ul {
        list-style: none;
        padding: 0;
        margin: 0;

        li.active {

            > ul {
                display: block;
            }
        }

        ul {
            display: none;
        }
    }

    // Define height

    li.level1 > a {
        @include font-size(20px, 20px);
        padding: 20px;

        @include breakpoint(mobile) {
            @include font-size(16px, 16px);
            padding: 18px 55px 18px 18px;
        }
    }

    li.level2 > a {
        @include font-size(20px, 20px);
        padding: 20px 16px 20px 20px;

        @include breakpoint(mobile) {
            @include font-size(14px, 14px);
            padding: 18px 55px 18px 18px;
        }
    }

    li.level3 > a {
        @include font-size(18px, 20px);
        padding: 20px 16px 20px 20px;

        @include breakpoint(mobile) {
            @include font-size(14px, 14px);
            padding: 18px 55px 18px 18px;
        }
    }

    li.current.active > a {
        color: #fff;
        text-decoration: none;
    }

    .icon-indicator {
        content: ' ';
        position: absolute;
        width: 60px;
        height: 100%;
        border-left: 1px solid $color--main-mega-border;
        right: 0;
        top: 0;
        z-index: 6;
        -webkit-transition: background-color .2s;
        transition: background-color .2s;
        @include breakpoint(mobile) {
            width: 50px;
        }

        &:before {
            @extend %icon, %icon-arrow-right;
            text-align: center;
            position: absolute;
            top: 21px;
            left: 0;
            right: 0;
            transition: transform .25s;
            font-size: 13px;
        }
        @include breakpoint(mobile) {

            &:after {
                height: 10px;
            }
        }
    }

    a,
    span.separator {
        position: relative;
        display: block;
        text-decoration: none;
        color: inherit;
        border-bottom: 1px solid $color--main-mega-border;

        &.open > .icon-indicator:before {
            transform: rotate(-90deg);
            transition: transform .25s;
        }
    }
}

// Mega menu style for main menu (main navigation)

.mega-menu--main-nav {
    color: $color--main-mega-text;

    li.level1 {

        > a {
            background: $color--main-mega-bg-l1;
        }

        > a.open {
            border-bottom-color: $color--main-mega-border-l2;

            .icon-indicator {
                background: $color--main-mega-bg-l2;
                border-left-color: $color--main-mega-border-l2;
            }
        }
    }

    li.level2 {

        > a {
            background: $color--main-mega-bg-l2;
            border-bottom-color: $color--main-mega-border-l2;

            .icon-indicator {
                border-left-color: $color--main-mega-border-l2;
            }
        }

        > a.open {
            border-bottom-color: $color--main-mega-border-l3;

            .icon-indicator {
                background: $color--main-mega-bg-l3;
                border-left-color: $color--main-mega-border-l3;
            }
        }
    }

    li.level3 > a {
        background: $color--main-mega-bg-l3;
        border-bottom-color: $color--main-mega-border-l3;
    }

    .icon-indicator {
        border-left-color: $color--main-mega-border;
    }

    a {
        border-bottom-color: $color--main-mega-border;
    }
}
