/*
==========================================================================
   #Page header
========================================================================== */
// TODO : remove unused styles and make sure colors are using variables
$header-padding-v: 20px;
$header-padding-h: 0;

.page-head {
    padding: $header-padding-v $header-padding-h;

    @include breakpoint(not-desktop) {
        position: fixed;
        left: 0;
        right: 0;
        z-index: z-index(header);
    }

    &.menu-open {
        height: 100%;

        .inner-wrapper {
            @include breakpoint(not-desktop) {
                height: 100%;
                overflow-y: scroll;
            }
        }
    }
}

.page-head-top {
    position: relative;
}
